@keyframes anm_load_rotate {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(360deg);
  }
}
@keyframes anm_opacity {
  from {
    opacity: 0%;
  }
  to {
    opacity: 100%;
  }
}
.def_container_1 {
  display: flex;
  flex-direction: row;
  column-gap: 10px;
  row-gap: 5px;
  flex-wrap: wrap-reverse;
  align-items: center;
}
.text_blue {
  color: #020095;
}
.text_blue--dark {
  color: #4bc6ff;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.visually_hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.svg_sprait {
  display: none;
}
.deactivated {
  opacity: 65%;
  cursor: no-drop;
  pointer-events: none;
}
*:disabled {
  opacity: 65%;
  cursor: no-drop;
  pointer-events: none;
}
button:disabled {
  border-color: #44444486;
  opacity: 65%;
  cursor: no-drop;
  pointer-events: none;
}
.scroll_remove {
  overflow: hidden;
}
.text_in_file {
  background-color: #f3f3f3;
  margin-top: 10px;
  margin-left: 20px;
  overflow: auto;
  outline-style: solid;
  outline-color: black;
  outline-width: 1px;
  font-size: 14px;
}
.text_in_file::-webkit-scrollbar {
  width: 2px;
  /* ширина scrollbar */
  height: 10px;
  transform: translateY(0px);
}
.text_in_file::-webkit-scrollbar-track {
  background-color: rgba(122, 122, 122, 0.5);
  /* цвет дорожки */
}
.text_in_file::-webkit-scrollbar-thumb {
  background: #464646;
  /* цвет плашки */
  border: none;
  /* padding вокруг плашки */
}
.text_in_file--dark {
  background-color: #282a36;
  color: #dbdbb7;
}
.text_in_file--dark::-webkit-scrollbar {
  width: 2px;
  /* ширина scrollbar */
  height: 10px;
  transform: translateY(0px);
}
.text_in_file--dark::-webkit-scrollbar-track {
  background: #ffffff17;
  /* цвет дорожки */
}
.text_in_file--dark::-webkit-scrollbar-thumb {
  background-color: #ffffff;
  border: none;
  /* padding вокруг плашки */
}
.text_in_file__text {
  margin: 0;
  padding: 10px;
}
.text_in_file__content_container:nth-of-type(n + 2) {
  border-top: 3px solid black;
}
@media (min-width: 2px) and (max-width: calc(440px - 1px)) {
  .text_in_file {
    margin-left: 0px;
  }
}
.file_info {
  width: 50%;
  background-color: #f3f3f3;
  padding: 10px;
  margin: 20px 0 10px 20px;
  overflow: hidden;
  outline-style: solid;
  outline-color: black;
  outline-width: 1px;
}
.file_info--dark {
  background-color: #282a36;
  color: #dbdbb7;
}
.file_info__name {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: calc(100%);
}
@media (min-width: 2px) and (max-width: calc(440px - 1px)) {
  .file_info {
    width: 100%;
    margin: 20px 0 10px 0px;
    position: relative;
  }
}
.file_info > p {
  margin: 0;
  padding: 0;
}
.def_icon__icon {
  width: 35px;
  height: 35px;
  fill: #000000;
}
.def_icon__icon--dark {
  fill: #ffffff;
}
.def_icon__icon--anim_1 {
  animation-name: anm_load_rotate;
  animation-duration: 1.4s;
  animation-iteration-count: infinite;
  animation-timing-function: cubic-bezier(0.24, 0.23, 0.65, 0.63);
}
.js-addon_dark_theme_button {
  min-width: 12px;
  min-height: 12px;
  border-radius: 50%;
  border-width: 3px;
  border-style: solid;
  width: 35px;
  height: 35px;
  opacity: 60%;
  line-height: 0;
  padding: 2px;
}
.js-addon_dark_theme_button__icons_wrapper {
  width: 100%;
  height: 100%;
}
.js-addon_dark_theme_button__icon_dark,
.js-addon_dark_theme_button__icon_white {
  width: 100%;
  height: 100%;
  display: block;
}
.js-addon_dark_theme_button[value="white"] {
  border-color: #585858;
}
.js-addon_dark_theme_button[value="white"] .js-addon_dark_theme_button__icon_dark {
  display: none;
}
.js-addon_dark_theme_button[value="dark"] {
  border-color: #9f9f9f;
}
.js-addon_dark_theme_button[value="dark"] .js-addon_dark_theme_button__icon_white {
  display: none;
}
.js-addon_dark_theme_button:hover {
  opacity: 100%;
}
/*
    кнопки, базовый стиль
*/
.def_btn {
  min-width: 150px;
  min-height: 26px;
  width: fit-content;
  padding: 3px 8px 3px 8px;
  background-color: #f0f0f0;
  font-family: sans-serif;
  font-size: 13px;
  border-radius: 6px;
  border-width: 1px;
  border-style: double;
  border-color: #444444;
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 0;
}
@media (min-width: 2px) and (max-width: calc(440px - 1px)) {
  .def_btn {
    font-size: 16px;
    width: 162px;
    padding: 5px 10px 5px 10px;
  }
}
.def_btn:hover {
  background-color: #e3e3e3;
}
.def_btn:active {
  background-color: #dbdbdb;
  border-color: #707070;
}
/*
    кноака загрузки фаила
*/
.js-file_uploader {
  width: fit-content;
}
.js-file_uploader:disabled + label.def_btn {
  border-color: #44444486;
  color: #b7b7b7;
  opacity: 65%;
  cursor: no-drop;
  pointer-events: none;
}
#id_file_uploader {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  position: absolute;
  z-index: -10;
}
#id_file_uploader:focus + label {
  outline-width: 1px;
  outline-style: auto;
}
.input_text {
  font-family: monospace;
  border-radius: 5px;
  border-width: 1px;
  border-style: solid;
  padding: 4px;
}
@media (min-width: 2px) and (max-width: calc(440px - 1px)) {
  .input_text {
    font-size: 16px;
  }
}
.body {
  box-sizing: border-box;
  margin: 0;
  min-width: 250px;
  width: 100vw;
  /* (чтоб на ширину не влиял скролл) ширина 100% экрана */
  min-height: 100vh;
  overflow-x: hidden;
  display: grid;
  justify-items: center;
  grid-template-rows: 0fr 1fr 0fr;
  grid-template-columns: 1fr;
  grid-auto-rows: auto;
  grid-auto-columns: auto;
  grid-template-areas: "grid_header" "grid_main" "grid_footer";
}
.body::-webkit-scrollbar {
  width: 10px;
  /* ширина scrollbar */
  height: 2px;
  transform: translateY(0px);
}
.body::-webkit-scrollbar-track {
  background-color: rgba(122, 122, 122, 0.5);
  /* цвет дорожки */
}
.body::-webkit-scrollbar-thumb {
  background: #464646;
  /* цвет плашки */
  border: none;
  /* padding вокруг плашки */
}
.body--dark {
  background-color: #232529;
}
.body--dark::-webkit-scrollbar {
  width: 10px;
  /* ширина scrollbar */
  height: 2px;
  transform: translateY(0px);
}
.body--dark::-webkit-scrollbar-track {
  background: #ffffff17;
  /* цвет дорожки */
}
.body--dark::-webkit-scrollbar-thumb {
  background-color: #ffffff;
  border: none;
  /* padding вокруг плашки */
}
.body__header {
  grid-area: grid_header;
  margin-top: 10px;
  align-self: start;
  width: 95%;
  max-width: 1000px;
}
@media (min-width: 2px) and (max-width: calc(440px - 1px)) {
  .body__header {
    width: 100%;
    margin-top: 0px;
  }
}
.body__main {
  margin-bottom: 20px;
  padding: 0 10px 10px 10px;
  grid-area: grid_main;
  align-self: start;
  width: 95%;
  /* задаем ширину */
  max-width: 1000px;
  /* максимальная ширина */
}
.body__footer {
  grid-area: grid_footer;
  width: 100%;
  align-self: end;
}
.header {
  background-color: #88ca88;
  height: fit-content;
  display: grid;
  grid-template-rows: 0fr 0fr;
  grid-template-columns: 1fr 1.3fr;
  grid-auto-rows: auto;
  grid-auto-columns: auto;
  align-items: end;
  grid-template-areas: "grid_header_h1 grid_header_sb" "grid_header_line grid_header_line";
}
@media (min-width: 2px) and (max-width: calc(440px - 1px)) {
  .header {
    grid-template-columns: 1fr 0fr;
  }
}
.header--dark {
  background-color: #406166;
  color: white;
}
.header__button_wrapper {
  display: none;
}
@media (min-width: 2px) and (max-width: calc(440px - 1px)) {
  .header__button_wrapper {
    grid-area: grid_header_sb;
    border-left: 1px black solid;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 10px 0 10px;
    width: 56px;
    justify-self: end;
  }
  .header__button_wrapper--dark {
    border-left: 1px #c7c7c7 solid;
  }
}
.header__head {
  text-align: center;
  font-size: 25px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-transform: uppercase;
  grid-area: grid_header_h1;
  white-space: nowrap;
  padding: 0px 10px 0px 10px;
  margin: 13px 0;
}
@media (min-width: 1200px) {
  .header__head {
    padding: 0px 10px 0px 30px;
  }
}
@media (min-width: 440px) and (max-width: calc(1200px - 1px)) {
  .header__sidebar {
    grid-area: grid_header_sb;
    overflow: auto;
    height: 100%;
  }
}
@media (min-width: 1200px) {
  .header__sidebar {
    grid-area: grid_header_sb;
    overflow: auto;
    height: 100%;
  }
}
.header__horisontal_line {
  margin: 0px 0px 23px 0px;
  border: none;
  height: 1px;
  width: 100%;
  background-color: #0e0e0e;
  grid-area: grid_header_line;
}
.header__horisontal_line--dark {
  background-color: #c7c7c7;
  height: 2px;
}
.main {
  background-color: #88ca88;
  overflow: hidden;
  height: fit-content;
}
@media (min-width: 2px) and (max-width: calc(440px - 1px)) {
  .main {
    width: 100%;
  }
}
.main__menu {
  margin: 0;
  padding: 0 0 0 10px;
}
.main--dark {
  background-color: #406166;
}
.footer {
  background-color: #464646;
  height: fit-content;
  min-height: 60px;
  display: flex;
  padding: 10px 30px;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 10px;
}
.footer--dark {
  box-shadow: inset #7e7e7e 0px 2px 0px 0px;
}
.menu {
  display: flex;
  column-gap: 10px;
  flex-direction: row-reverse;
  justify-content: space-between;
  flex-wrap: nowrap;
  align-items: flex-start;
}
.menu__icons_container {
  display: flex;
  gap: 10px;
  justify-content: flex-start;
  flex-direction: column;
}
.menu__handle_container {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  flex-wrap: nowrap;
  row-gap: 10px;
}
.link_icon {
  line-height: 0px;
}
.link_icon__icon {
  width: 30px;
  height: 30px;
  transition: all ease 0.3s;
  fill: #ffffff;
}
.link_icon__icon:hover {
  fill: #6be127;
}
